import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { LinkBudgetScenario, Point } from './LinkBudgetScenario.model';
import * as d3 from 'd3';

@Component({
  selector: 'app-root',
  encapsulation: ViewEncapsulation.None,
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {
  points: Point[];

  form: FormGroup;
  private powerOut: number;
  private powerIn: number;
  private DefaultLBScenario: LinkBudgetScenario;

  private height: number;
  private width: number;
  private margin: { top: number; bottom: number; left: number; right: number };

  //Parameters used by D3 #FIXME: Can we replace the anys types?
  //The x and y axis used by D3
  private x: any;
  private y: any;
  private budget: any;
  svg: any;
  drawing: any;
  graph: any;

  constructor(private formBuilder: FormBuilder) {
    this.powerIn = 10;
    this.form = this.formBuilder.group({
      power: this.powerIn,
      gains: this.formBuilder.group({
        cableTx: -3,
        antTx: 30,
        fsl: -209,
        antRx: 26,
        cableRx: -5,
      }),
    });
  }

  ngOnInit(): void {
    this.DefaultLBScenario = new LinkBudgetScenario(this.powerIn);
    this.DefaultLBScenario.addComponents([
      {
        id: 'tx',
        component: {
          label: 'Ptx',
          gain: 0,
          linear: true,
          position: 0,
          width: 15,
        },
      },
      {
        id: 'cableTx',
        component: {
          label: 'Ltx',
          gain: -3,
          linear: true,
          position: 10,
          width: 10,
        },
      },
      {
        id: 'antTx',
        component: {
          label: 'Gtx',
          gain: 24,
          linear: false,
          position: 25,
          width: 10,
        },
      },
      {
        id: 'fsl',
        component: {
          label: 'Lpath',
          gain: -125,
          linear: true,
          position: 35,
          width: 30,
        },
      },
      {
        id: 'antRx',
        component: {
          label: 'Grx',
          gain: 35,
          linear: false,
          position: 65,
          width: 10,
        },
      },
      {
        id: 'cableRx',
        component: {
          label: 'Lrx',
          gain: -5,
          linear: true,
          position: 75,
          width: 10,
        },
      },
      {
        id: 'rx',
        component: {
          label: 'Prx',
          gain: 0,
          linear: true,
          position: 85,
          width: 15,
        },
      },
    ]);
    this.DefaultLBScenario.compute();
    this.drawGraph();
  }

  randomizeInputs() {
    this.DefaultLBScenario.randomize();
    this.DefaultLBScenario.compute();
    this.updateData();
  }

  onSubmit() {
    this.DefaultLBScenario.setPowerIn(<number>this.form.value['power']);

    let formValues = this.form.value['gains'];
    this.DefaultLBScenario.editGains(formValues);
    this.DefaultLBScenario.compute();
    this.updateData();
  }
  private addLabels() {
    let labels = this.DefaultLBScenario.getLabels();

    this.svg
      .append('g')
      .attr('class', 'labels')
      .attr('transform', 'translate(0,-10)')
      .selectAll('text')
      .data(labels)
      .enter()
      .append('text')
      .text((d) => d.name)
      .attr('x', (d) => d.position * 9)
      .attr('text-anchor', 'middle');
  }
  private drawGraph() {
    this.points = this.DefaultLBScenario.getPoints();

    (this.margin = { top: 30, right: 30, bottom: 0, left: 30 }),
      (this.width = 960 - this.margin.left - this.margin.right), //Same value as for the drawing
      (this.height = 240 - this.margin.top - this.margin.bottom);

    this.x = d3.scaleLinear().range([0, this.width]);
    this.y = d3.scaleLinear().range([0, this.height]);
    this.x.domain([0, 100]);

    this.svg = d3
      .select('#diagram')
      .append('svg')
      .attr('id', 'graph')
      .attr(
        'viewBox',
        -this.margin.left +
          ' ' +
          -this.margin.top +
          ' ' +
          (this.width + this.margin.left + this.margin.right) +
          ' ' +
          (this.height + this.margin.top + this.margin.bottom + 60)
      );

    this.svg
      .append('path')
      .attr('width', this.width + this.margin.left + this.margin.right)
      .attr('height', this.height + this.margin.top + this.margin.bottom);

    this.budget = this.svg
      .append('text')
      .attr('x', (this.width + this.margin.left + this.margin.right) / 2)
      .attr('y', this.height + this.margin.top + this.margin.bottom + 15)
      .attr('text-anchor', 'middle');

    //The vertical lines, here the *9 factor is explained by the 100px -> (960-60)px conversion
    this.svg
      .append('g')
      .attr('class', 'group')
      .selectAll('line')
      .data(this.points)
      .enter()
      .append('line')
      .attr('class', 'vert-line')
      .attr('x1', (d) => d.x * 9)
      .attr('y1', this.height)
      .attr('x2', (d) => d.x * 9)
      .attr('y2', 0)
      .attr('stroke-width', 1)
      .attr('stroke-dasharray', '5,5')
      .attr('stroke', '#888888');

    this.svg.append('g').attr('class', 'YAxis');

    this.addLabels();
    this.updateData();
  }

  private updateData() {
    let x = this.x; //FIXME: Could be better here
    let y = this.y;
    this.points = this.DefaultLBScenario.getPoints();
    this.powerOut = this.DefaultLBScenario.getPowerOut();
    //Careful with this one, returns (min, max) but domain expects (up, down) so trouble with yaxis
    let [min, max] = <[number, number]>(
      d3.extent(this.points, (point: { x: number; y: number }) => point.y)
    ) ?? [0, -150];

    this.y.domain([max, min]);

    var valueline = d3
      .line()
      .x(function (d: any) {
        return x(d.x) ?? x(0);
      })
      .y(function (d: any) {
        return y(d.y) ?? y(0);
      });

    //Adding the plot line
    this.svg
      .select('path')
      .datum(this.points)
      .attr('class', 'line')
      .transition()
      .duration(1000)
      .attr('d', <any>valueline);
    //Adding the YAxis
    this.svg
      .select('.YAxis')
      .transition()
      .duration(1000)
      .call(d3.axisLeft(this.y));
    //Adding the formula at bottom
    this.budget.text(
      'Prx = Ptx - Ltx + Gtx - Lpath + Grx - Lrx = ' +
        this.powerOut.toFixed(1) +
        ' dB'
    );
  }
}
